import styled from "styled-components";

interface VodRow {
    name: string,
    image: string,
    fire: Spec[],
    frost: Spec[],
    arcane: Spec[],
}

interface Spec {
    displayName: string,
    link: string
}

interface Props {
    vods: VodRow[]
}
export default function VODsComponent(props: Props) {
    const vods = props.vods
    return (
        <StyledVodDiv className={'vods-div'}>
            <StyledTable>
                <tr>
                    <StyledTableHeading/>
                    <StyledTableHeading/>
                    <StyledTableHeading>Arcane</StyledTableHeading>
                    <StyledTableHeading>Frost</StyledTableHeading>
                    <StyledTableHeading>Fire</StyledTableHeading>
                </tr>
                {
                    vods.map(vod => {
                        return <tr>
                            <StyledTableData className={"boss-icon"}>
                                <img src={`bosses/${vod.image}`} className={"boss-image"} alt={vod.name} />
                            </StyledTableData>
                            <StyledTableData className={"boss-square"}>
                                <div className={"boss-name"}>{vod.name}</div>
                            </StyledTableData>
                            <StyledTableData className={"spec-square arcane"}>
                                {vod.arcane.map(it => { return link(it.link, it.displayName) })}
                            </StyledTableData>
                            <StyledTableData className={"spec-square frost"}>
                                {vod.frost.map(it => { return link(it.link, it.displayName) })}
                            </StyledTableData>
                            <StyledTableData className={"spec-square fire"}>
                                {vod.fire.map(it => { return link(it.link, it.displayName) })}
                            </StyledTableData>
                        </tr>
                    })
                }
            </StyledTable>
        </StyledVodDiv>


    )
}

function link(url: string, player: string) {
    return (<StyledRowDiv className="rowContent"><StyledLink href={url}>{player}</StyledLink></StyledRowDiv>)
}

export const StyledVodDiv = styled.div`
    margin: auto;
    width: 80%;
    tr:nth-child(even) {
        background-color: #37393f;
    }

    .boss-square {
        width: 18%;
    }

    .boss-icon {
        width: 5%;
    }

    .boss-square .boss-image {
        float: left
    }

    .boss-square .boss-name {
        margin-top: auto;
        margin-bottom: auto;
        padding: 10px;
    }
      
    a:hover {
        font-size: 16px;
        border: none;
        outline: none;
        color: white;
    }
      
    .frost a:hover {
        background-color: #40C7EB;
        color: #333
    } 
      
    .arcane a:hover {
        background-color: #d77ef8;
        color: #333
    } 
      
    .fire a:hover {
        background-color: #ff9900;
        color: #333
    } 
`

export const StyledRowDiv = styled.div`
    padding: 1px;
    margin: 9px;
`

export const StyledTable = styled.table`
    width: 100%;
    border-collapse: collapse;
`

export const StyledTableHeading = styled.th`
    padding: 12px 4px;
`

export const StyledTableData = styled.td`
    border-top: 0.5px solid #5c5c5c;
    border-bottom: 0.5px solid #5c5c5c;
`

export const StyledLink = styled.a`
    font-size: 16px;
    border: none;
    outline: none;
    color: white;
    padding: 7px 20px;
    border-radius: 5px;
`
